import {estimate, harvest, makePlant, makeSeed, makeStack, putInto, RULES, run, takeFrom} from "@/model/plant";
import {randArr} from "@/util/number";


export default {
    state: {
        coins: 5000,
        slots: [],
        storage: [],
        timestamp: Date.now(),
        pid: null,
    },
    mutations: {
        harvest(state, index) {
            let plant = state.slots[index].plant;
            if (plant) {
                state.slots[index].plant = null;
                let products = harvest(plant);
                putInto(state.storage, ...products);
                state.coins += products.coins || 0;
            }
        },
        collect({slots, storage}, index) {
            let plant = slots[index].plant;
            if (plant) {
                slots[index].plant = null;
                putInto(storage, makeStack(plant));
            }
        },
        sell(state, index) {
            let stack = takeFrom(state.storage, index, 1);
            if (stack) {
                let value = estimate(stack.item);
                if (value !== null) {
                    state.coins += value;
                } else {
                    putInto(state.storage, stack);
                }
            }
        },
        sellAll(state, index) {
            let stack = takeFrom(state.storage, index);
            if (stack) {
                let value = estimate(stack.item);
                if (value !== null) {
                    state.coins += value * stack.amount;
                } else {
                    putInto(state.storage, stack);
                }
            }
        },
        buySeed(state, option = 'normal') {
            switch (option) {
                case "normal": if (state.coins < 100) {return;} state.coins -= 100; break;
                case "long-gene": if (state.coins < 400) {return;} state.coins -= 400; break;
                case "high-value": if (state.coins < 200) {return;} state.coins -= 200; break;
            }
            let seed;
            switch (option) {
                case "normal": seed = makeSeed(randArr(10, 5, 0x0F, 0), 25); break;
                case "long-gene": seed = makeSeed(randArr(20, 15, 0x0F, 0), 100); break;
                case "high-value": seed = makeSeed(randArr(10, 5, 0x2F, 0), 50); break;
            }
            putInto(state.storage, makeStack(seed));
        },
        buySlot(state) {
            if (state.coins >= 1000) {
                state.coins -= 1000;
                state.slots.push({index: state.slots.length, plant: null});
                return true;
            }
        },
        useAt({slots, storage}, {slotIndex, storageIndex}) {
            let slot = slots[slotIndex];
            let stack = storage[storageIndex];
            let rule = RULES[stack.item.type];
            if (rule && rule.onUse) {
                let cost = rule.onUse(stack.item, slot);
                if (cost) {
                    takeFrom(storage, storageIndex, cost);
                }
             }
        },
        createNewPlant({storage}, option = 'normal') {
            let plant;
            switch (option) {
                case "normal": plant = makePlant(Array(10).fill(0).map(() => Math.floor(Math.random() * 0x0F))); break;
                case "long-gene": plant = makePlant(Array(20).fill(0).map(() => Math.floor(Math.random() * 0x0F))); break;
                case "high-value": plant = makePlant(Array(10).fill(0).map(() => Math.floor(Math.random() * 0x1f))); break;
            }
            putInto(storage, makeStack(plant));
        },
        tick(state, dt) {
            const slots = state.slots;
            for (let i = 0; i < dt; i++) {
                slots.forEach(slot => {
                    if (slot.plant) {
                        run(slot.plant)
                    }
                });
            }
            state.timestamp = Date.now();
        },
        setPid(state, pid) {
            state.pid = pid;
        },
        fillState(state, newState) {
            Object.assign(state, newState);
        }
    },
    actions: {
        start({dispatch, commit, state}) {
            dispatch('load');
            if (state.pid !== null) {
                clearInterval(state.pid);
            }
            let pid = setInterval(() => {
                commit('tick', 1);
                dispatch('save');
                }, 60000);
            commit('setPid', pid);
        },
        stop({dispatch, commit, state}) {
            if (state.pid !== null) {
                clearInterval(state.pid);
                commit('setPid', null);
            }
            dispatch('save');
        },
        load({commit}) {
            let data = JSON.parse(localStorage.getItem('n-farm-data')) || {};
            commit('fillState', data);
            if (data.timestamp) {
                const dt = Math.floor((Date.now() - data.timestamp) / 60000);
                commit('tick', dt);
            }
        },
        save({state}) {
            let data = JSON.stringify({
                coins: state.coins,
                slots: state.slots,
                storage: state.storage,
                timestamp: state.timestamp,
            });
            localStorage.setItem('n-farm-data', data);
        },
        clear({commit}) {
            let data = JSON.stringify({
                coins: 0,
                slots: [],
                storage: [],
            });
            commit('fillState', data);
            localStorage.removeItem('n-farm-data');
        },
    },
};